import React, { useState, useEffect } from "react";
import { db } from "./firebase"; // Import Firestore instance
import { doc, setDoc } from "firebase/firestore"; // Firestore methods
import { getAuth } from "firebase/auth"; // Firebase Auth
import "./Invite.css"; // Add styling in Invite.css

const Invite = () => {
  const [inviteCode, setInviteCode] = useState(""); // State to store invite code
  const [inviteLink, setInviteLink] = useState(""); // State to store the full invite link
  const [message, setMessage] = useState(""); // State for success/error message
  const [userId, setUserId] = useState(null); // State to store user ID

  // Get the current user's ID using Firebase Authentication
  useEffect(() => {
    const auth = getAuth();
    const currentUser = auth.currentUser;
    if (currentUser) {
      setUserId(currentUser.uid); // Set user ID when user is logged in
    } else {
      setMessage("User not authenticated.");
    }
  }, []);

  // Function to generate a random invite code
  const generateInviteCode = () => {
    const characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
    let code = "";
    for (let i = 0; i < 8; i++) {
      code += characters.charAt(Math.floor(Math.random() * characters.length));
    }
    setInviteCode(code); // Update invite code
    generateInviteLink(code); // Generate invite link
  };

  // Function to generate the full invite link
  const generateInviteLink = (code) => {
    const link = `${window.location.origin}/join/${code}`; // Customize the URL if necessary
    setInviteLink(link); // Update invite link state
  };

  // Function to store the invite code and link in Firestore
  const storeInviteInDatabase = async (code, link) => {
    if (!userId) return; // Check if user is authenticated
    try {
      // Define Firestore document reference using the dynamic user ID
      const userRef = doc(db, "users", userId);
  
      // Update or set the invite code and link for the specific user at the top level
      await setDoc(userRef, {
        inviteCode: code,
        inviteLink: link,
        createdAt: new Date(),
      }, { merge: true }); // Merge the new invite data with existing user data

      setMessage("Invite code saved to database successfully!");
    } catch (error) {
      console.error("Error storing invite in database: ", error);
      setMessage("Failed to save invite code to database.");
    }
  };

  // Function to copy the invite link to the clipboard
  const copyInviteLink = () => {
    navigator.clipboard.writeText(inviteLink)
      .then(() => alert("Invite link copied to clipboard"))
      .catch((error) => alert("Failed to copy the invite link"));
  };

  // Function to handle the invitation process
  const handleGenerateAndStore = () => {
    generateInviteCode(); // Generate invite code
    if (inviteCode && inviteLink) {
      storeInviteInDatabase(inviteCode, inviteLink); // Store in Firestore
    }
  };

  // Function to handle saving the generated invite
  const handleSaveInvite = () => {
    if (inviteCode && inviteLink) {
      storeInviteInDatabase(inviteCode, inviteLink); // Save the invite code and link in the database
    } else {
      setMessage("Please generate an invite code first.");
    }
  };

  return (
    <div className="invite-container">
      <h2>Generate Your Invite Code</h2>
      <div className="generate-container">
        <button onClick={handleGenerateAndStore} className="generate-btn">
          Generate and Store Invite Code
        </button>
        {message && <p className="message">{message}</p>}
        {inviteCode && (
          <div className="invite-info">
            <p><strong>Invite Code:</strong> {inviteCode}</p>
            <p><strong>Invite Link:</strong> 
              <span>{inviteLink}</span>
            </p>
            <button onClick={copyInviteLink} className="copy-btn">
              Copy Invite Link
            </button>
          </div>
        )}
        {inviteCode && inviteLink && (
          <button onClick={handleSaveInvite} className="save-btn">
            Save Invite to Database
          </button>
        )}
      </div>
    </div>
  );
};

export default Invite;
